import React from 'react'

import { AboutSection, Avatar, Title, Text, SubTitle } from './style'
import { SectionIntro, ContainerLayout, ResumeButton } from 'components/common'

import me from 'assets/img/me.jpg'

const AboutPage = () => {
  return (
    <>
      <SectionIntro>
        <ContainerLayout>
          <AboutSection>
            <div>
              <Avatar src={me} alt="user photo" />
              <SubTitle> Software Developer</SubTitle>
            </div>
            <div>
              <Title> Hello, I’m Nestor </Title>

              <Text>
                {' '}
                I'm a developer passionate about developing Full Stack applications with free and open source technologies from <b className="text-primary lined-link">Colombia</b> with experience in the design, development, analysis and implementation of monolithic and client-server applications.
                With knowledge of JavaScript, TypeScript, React, Node.js, relational and non-relational databases; SonarQube, Docker, CI / CD (GitLab) using agile methodologies.
              </Text>
              <ResumeButton href="https://bit.ly/3FZSVca" target="_blank">
                {' '}
                Download resume{' '}
              </ResumeButton>
            </div>
          </AboutSection>
        </ContainerLayout>
      </SectionIntro>
    </>
  )
}

export default AboutPage
