import React from 'react'

import Navbar from './navbar'
import Footer from './footer'

import { GlobalStyle, ContainerLayout, MainContent } from '../common'

interface Prop {
  children: any
}

const Layout: React.FC<Prop> = ({ children }) => {
  return (
    <>
      <GlobalStyle />
      <MainContent>
        <ContainerLayout>
          <Navbar siteTitle={'Nestor Cortina'} />
        </ContainerLayout>
        <ContainerLayout>
          <main>{children}</main>
        </ContainerLayout>
      </MainContent>
      <Footer />
    </>
  )
}

export default Layout
