import React from 'react'

import { Tag, ContainerLayout, WorkPost, Category, Intro, SubTitle, Title, Text } from 'components/common'
import { WorkImage } from './style'
import works from 'data/projects'

const PortfolioPage = () => {
  return (
    <>
      <Intro>
        <ContainerLayout>
          <SubTitle className="text-dark">Selected Work</SubTitle>

          <ContainerLayout className="wrapper">
            {works.map((work, index) => {
              const { title, sourceCode, image, category, description, tags, demoView } = work
              const urlDemoView = demoView === null ? '#' : demoView
              const targetDemoView = demoView === null ? '_self' : '_blank'
              return (
                <WorkPost key={`work-${index}`}>
                  <div className="media">
                    <div className="image-wrapper">
                      <a href={urlDemoView} target={targetDemoView}>
                        <WorkImage src={image} title="work title" />
                      </a>
                    </div>
                  </div>
                  <div className="content">
                    <header>
                      <Category>{category}</Category>
                      <Title>
                        <a className="text-primary lined-link" style={{ boxShadow: 'none' }} href={sourceCode} target="_blank">
                          {title}
                        </a>
                      </Title>
                    </header>
                    <Text
                      dangerouslySetInnerHTML={{
                        __html: description
                      }}
                    />
                    <div>
                      {tags.map((tag, index) => (
                        <Tag key={`tag-${index}`}>{tag}</Tag>
                      ))}
                    </div>
                  </div>
                </WorkPost>
              )
            })}
          </ContainerLayout>
        </ContainerLayout>
      </Intro>
    </>
  )
}

export default PortfolioPage
