import React from 'react'
import { Link } from 'react-router-dom'

import { Smile } from 'react-feather'

import { NavbarElement, NavbarList, NavbarLogo } from './style'
import { routes } from 'routes'

interface Prop {
  siteTitle: string
}

const Navbar: React.FC<Prop> = ({ siteTitle }) => {
  const currentPath = location.pathname

  const isActive = (path: string) => {
    return currentPath === path ? 'active' : ''
  }

  return (
    <nav>
      <div className="container">
        <NavbarElement>
          <NavbarLogo>
            <h3>
              <Link to="/">
                <Smile className="align-middle" /> <span className="align-middle"> {siteTitle} </span>
              </Link>
            </h3>
          </NavbarLogo>
          <div className="main-navigation">
            <NavbarList>
              {routes.map(({ path, Icon, title }, index) => (
                <li key={`nav-item-${index}`}>
                  <Link to={path} className={`lined-link ${isActive(path)}`}>
                    {' '}
                    {Icon} <span> {title} </span>{' '}
                  </Link>
                </li>
              ))}
            </NavbarList>
          </div>
        </NavbarElement>
      </div>
    </nav>
  )
}

export default Navbar
